}

/* .create__product-container {
  margin: 10px auto;
  width: 90%;
  height: 100%;
} */

.create__product-form {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
}

.create__product-container-leftSection {
  position: relative;
  flex: 0.4 1;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.create__product-leftSection-image-input {
  position: relative;
  width: 50%;
  min-width: 250px;
  min-height: 270px;

  height: 300px;
  background: purple;
  /* min-height: 250px;
  max-height: 400px;
  height: 100%; */
}

.create__product-image-preview {
  width: 50%;
  min-width: 250px;
  min-height: 270px;

