  position: relative;

  /* width: 50%; */
  /* min-width: 240px; */
  /* height: 50%; */
  /* min-height: 250px;
  max-height: 400px; */
  /* position: absolute; */
}

.create__product-leftSection-image-input::before {
  content: "+";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* left: 0;
  right: 0; */
  /* height: 50%; */
  color: rgb(214, 212, 212);
  /* top: 0; */
  /* min-height: 250px;
  max-height: 400px; */
