/* ------------- right Section ------------- */

.create__product-container-rightSection {
  margin-top: 30px;
  flex: 0.4 1;
  display: flex;
  flex-direction: column;
}

.create__product-input {
  margin: 8px 10px;
}

.create__product-input input {
  width: 90%;
  padding: 10px 20px;
