}

.allproducts {
  width: 100%;
  height: 100%;
  position: relative;
  margin-top: 30px;
}

.allproducts__container {
  display: flex;
  flex-wrap: wrap;
}

.allproducts__product {
  margin: 2px;
  flex: 1 1 30%;
  min-width: calc(270px / 1.08);
  position: relative;
  top: 0;
}

.allproducts__items {
  height: 300px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  margin: 10px 20px;
  box-shadow: 1px 5px 10px lightgray;
}

.allproducts__items__image {
  flex: 0.6 1;
  object-fit: cover;
  overflow: hidden;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.allproducts__items__image:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.allproducts__items__text {
  flex: 0.4 1;
  z-index: 1;
  padding: 10px;
  line-height: 1.2;
}

.allproducts__items__title {
  font-size: 1.4rem;
  letter-spacing: 1px;
  font-weight: 700;
}

.allproducts__items__description {
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: 0.3px;
  font-family: "Montserrat";
  color: grey;
}

.allproducts__items__price-rating {
