  outline: none;
  transition: all 0.2s linear;
  cursor: pointer;
  letter-spacing: 0.8px;
}

.allproducts__items__button button:hover {
  background: dodgerblue;
  border: 1px solid dodgerblue;
  color: white;
}

.allproducts__edit-delete {
  position: absolute;
  right: 20px;
  top: 20px;
}

.allproducts__edit-delete button {
  border: none;
  outline: none;
  color: white;
