.order__category:hover::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 21;
  height: 2px;
  width: 100%;
  background: #d70f64;
  -webkit-animation-name: category-border;
          animation-name: category-border;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
}

@-webkit-keyframes category-border {
  from {
    width: 30%;
    left: 50%;
  }
  to {
    width: 100%;
    left: 0;
  }
}

@keyframes category-border {
  from {
    width: 30%;
    left: 50%;
  }
  to {
    width: 100%;
    left: 0;
  }
}

