.loading {
  width: 60px;
  height: 60px;
  border: none;
  border-top: 2px solid dodgerblue;
  border-left: 2px solid dodgerblue;
  margin: 20px auto;
  border-radius: 50%;
  -webkit-animation: check 0.7s forwards infinite linear;
          animation: check 0.7s forwards infinite linear;
}

.loadings {
  width: 20px;
  height: 20px;
  border: none;
  border-top: 2px solid dodgerblue;
  border-left: 2px solid dodgerblue;
  margin: 0px !important;
  border-radius: 50%;
  -webkit-animation: check 0.7s forwards infinite linear;
          animation: check 0.7s forwards infinite linear;
}

@-webkit-keyframes check {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes check {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

